.social-share{
	@include safeFlex(row,wrap,start,start);
	a{
		display: block;
		font-size: 38px;
		padding-right: 8px;
		&.facebook-share{
			color: #4267B2;
		}
		&.twitter-share{
			color: #1DA1F2;
		}
		&.linkedin-share{
			color: #2867B2;
		}
		&.email-share{
			color: $link;
		}
		&:hover,&:focus{
			color: $hoverLink;
		}
		&:last-child {
			padding-right: 0;
		}
	}
}