}

.drawer-toggle:after {
  content: '';
  display: block;
  width: 2rem;
  height: 2rem;
  position: absolute;
  bottom: calc(50% - 1rem);
  right: 0;
  background-image: url("../images/form-elements/caret-down-red.svg");
  background-position: center;
  background-size: 14px 14px;
  background-repeat: no-repeat;
}

