  /* Opera 11.10+ */
  background: -ms-linear-gradient(bottom, #f9f9f9 0%, transparent 100%);
  /* IE10+ */
  background: linear-gradient(to top, #f9f9f9 0%, transparent 100%);
  /* W3C */
}

