/* =================
   IMAGE WIDGET
==================== */

.paragraph--type--image{

	//Wrapper sizing (needs to be changed when image styles are changed or added to widget)
	.image-wrapper{
		@include auto;
		&.small{
			max-width: 280px;
		}
		&.medium{
			max-width: 480px;
		}
		&.large{
			max-width: 640px;
		}
		&.default{
			img{
				width: auto;
				@include auto;
			}
		}
	}
	
	//hover action when link
	a{
		color: $baseText;
		text-decoration: none;
		img{
			border: 1px solid $offWhite;
		}
		&:hover{
			img{
				border-color: $borderRule;
				opacity: .7;
				@include transAll(.3s);
			}
		}
	}
	
}//end image widget

